/*≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡
// Component .c-
≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡≡*/
.c-btn {
  display: block;
  width: 100%;
  padding: 15px 15px;
  font-weight: bold;
  text-align: center;
}

.c-btn + .c-btn {
  margin-top: 15px;
}

.c-btn--silver {
  border: 2px solid #cecece;
  background-color: #7f7f7f;
  color: #fff;
}

.c-btn--pink {
  border: 2px solid #cecece;
  background-color: #a75d76;
  color: #fff;
}

.c-btn--white {
  border: 2px solid #a75d76;
  background-color: #fff;
  color: #a75d76;
}

.c-btn--white.is-alert {
  position: relative;
}

.c-btn--white.is-alert::after {
  content: "";
  position: absolute;
  top: -7px;
  right: -3px;
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
}

.c-loading {
  position: fixed;
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 100;
}

.c-loading__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.c-loading__text h2 {
  font-size: 2.2rem;
}

.c-loading__text h3 {
  font-size: 1.6rem;
}

.c-loading__logo {
  position: absolute;
  left: 50%;
  bottom: 30px;
  transform: translateX(-50%);
  max-width: 20%;
}

.c-loading__logo img {
  -webkit-animation-name: spinner;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-duration: 4s;
  animation-name: spinner;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-duration: 4s;
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -ms-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

/* WebKit and Opera browsers */
@-webkit-keyframes spinner {
  from {
    -webkit-transform: rotateY(0deg);
  }
  to {
    -webkit-transform: rotateY(-360deg);
  }
}

/* all other browsers */
@keyframes spinner {
  from {
    -moz-transform: rotateY(0deg);
    -ms-transform: rotateY(0deg);
    transform: rotateY(0deg);
  }
  to {
    -moz-transform: rotateY(-360deg);
    -ms-transform: rotateY(-360deg);
    transform: rotateY(-360deg);
  }
}

.c-post__item {
  position: relative;
  margin-top: 12px;
  padding: 10px;
  background: #fff;
  border: 1px solid #d8d8d8;
  border-radius: 8px;
}

.c-post__status {
  display: inline-block;
  padding: 2px 40px;
  border-radius: 20px;
  color: #fff;
  font-weight: bold;
  margin-bottom: 10px;
}

.c-post__status--consideration {
  background-color: #a75d76;
}

.c-post__status--adopted {
  background-color: #7f7f7f;
}

.c-post__status--recruitment {
  background-color: #a5a5a5;
}

.c-post__posted {
  font-weight: 400;
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.c-post__content {
  overflow: hidden;
  min-height: 54px;
  margin-bottom: 15px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* number of lines to show */
  line-clamp: 3;
  -webkit-box-orient: vertical;
}

.c-post__answer {
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.c-input {
  width: 100%;
  outline: none;
  border: 1px solid #7f7f7f;
  color: #7f7f7f;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 15px;
}

.c-input--select {
  min-height: 40px;
}

.c-input--textarea {
  min-height: 200px;
}

.c-opinion {
  padding: 4%;
}

.c-opinion__icon {
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #a75d76;
}

.c-opinion__icon i {
  margin-right: 8px;
  color: #a75d76;
  font-size: 2rem;
}

.c-opinion__icon span {
  color: #a75d76;
  font-weight: bold;
}

.c-opinion__number {
  text-align: center;
  padding: 20px 0;
  color: #7f7f7f;
  letter-spacing: 5px;
}

.c-opinion__number span {
  color: #000;
  font-size: 1.8rem;
}

.c-opinion__body {
  margin-bottom: 40px;
  text-align: center;
}

.c-opinion__body .c-btn--silver {
  margin: 0 auto;
  max-width: 80%;
}

.c-header {
  padding: 15px 0;
  max-width: 768px;
  width: 100%;
  position: fixed;
  top: 0;
  border-bottom: 2px solid #d5d5d5;
}

.c-header__menu {
  overflow: auto;
  max-width: 768px;
  width: 100%;
  height: 100%;
  position: fixed;
  top: -100%;
  z-index: 101;
  background: #4f65a2;
  transition: all 0.2s linear;
  opacity: 0;
}

.c-header__menu.is-active {
  opacity: 1;
  height: calc(100% - (59px + 60px));
  top: 59px;
  bottom: 70px;
}

.c-header__menu li {
  margin: 0 10px;
  padding: 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #fff;
}

.c-header__menu li a {
  color: #fff;
  text-align: center;
  display: block;
  width: 100%;
  padding: 15px 15px;
  font-size: 1.6rem;
}

.c-headerv2 {
  background: #fff;
  position: fixed;
  z-index: 999;
  top: 0;
  padding: 15px 30px;
  max-width: 768px;
  width: 100%;
  border-bottom: 2px solid #d5d5d5;
}

.c-headerv2__back {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 15px;
  display: block;
  width: 30px;
  font-size: 2rem;
}

.c-headerv2 p {
  color: #323232;
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
}

.menu-trigger,
.menu-trigger span {
  display: inline-block;
  transition: all .4s;
  box-sizing: border-box;
}

.menu-trigger {
  margin-left: 15px;
  position: relative;
  z-index: 101;
  width: 44px;
  height: 25px;
  background: none;
  border: none;
  appearance: none;
  cursor: pointer;
}

.menu-trigger span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #000;
  border-radius: 4px;
}

.menu-trigger span:nth-of-type(1) {
  top: 0;
}

.menu-trigger span:nth-of-type(2) {
  top: 11px;
}

.menu-trigger span:nth-of-type(3) {
  bottom: 0;
}

.menu-trigger.active span:nth-of-type(1) {
  transform: translateY(11px) rotate(-45deg);
}

.menu-trigger.active span:nth-of-type(2) {
  left: 50%;
  opacity: 0;
  animation: active-menu-bar02 .8s forwards;
}

.menu-trigger.active span:nth-of-type(3) {
  transform: translateY(-11px) rotate(45deg);
}

@keyframes active-menu-bar02 {
  100% {
    height: 0;
  }
}

